import { HubPopover, HubPopoverProps } from '@hubert/atoms';
import { trigger } from '@hubert/styles';
import {
  CHILDREN,
  COLOR,
  COLOR_LEVEL,
  ELEMENT,
  FUNCTION,
  HEIGHT,
  PADDING,
  RADIUS,
  WIDTH,
  factoryMeta
} from '~/utils';

export const HubPopoverConfig = factoryMeta<HubPopoverProps>({
  title: 'Design System/Atoms/HubPopover',
  component: HubPopover,
  description: 'import { HubPopover } from "@hubert/atoms";',
  args: {
    trigger: 'hover'
  },
  props: {
    source: {
      description: 'Controle manual do popover',
      type: 'boolean'
    },
    trigger: {
      description:
        'Inicia aberto, ao disparar o "trigger" essa prop  ignorada, utilizando apenas a "opened"',
      type: 'inline-radio',
      options: trigger
    },
    color: COLOR,
    colorLevel: COLOR_LEVEL,
    pad: PADDING,
    radius: RADIUS,
    noOverflow: {
      description: 'No permite esconder conteudo e criar barra de scroll',
      type: 'boolean'
    },
    width: WIDTH,
    height: HEIGHT,
    onClose: FUNCTION,
    content: {
      ...ELEMENT,
      description: 'Elemento inserido no conteudo do popover'
    },
    children: {
      ...CHILDREN,
      description: 'Elemento que ser utilizado como trigger do popover'
    }
  }
});
