import { HubAnimation } from '@hubert/atoms';
import { size } from '@hubert/styles';
import { Meta } from '@storybook/react';

import { replaceAll, titleCase } from '@hubert/shared';
import data from './data';

export type HubAnimationProps = React.ComponentProps<typeof HubAnimation>;

export const HubAnimationStoryConfig: Meta<HubAnimationProps> = {
  title: 'Design System/Atoms/HubAnimation',
  component: HubAnimation,
  parameters: {
    docs: {
      description: {
        component: `
    import { HubAnimation } from "@hubert/atoms";
    import AnimationName from '@hubert/styles/lib/animations/<animation-name>.json';
        `
      },
      transformSource: (snippet, ctx) => {
        if (ctx.name === 'Default') {
          const el = document.getElementById(`story--${ctx.id}`);
          const iconName = el?.children?.[0]?.children?.[1]?.['title'];
          return String(snippet)
            .replace(/source={{[^]*}}/gi, `source={${titleCase(iconName)}}`)
            .split('\n')
            .filter((row) => !row.includes('RemoveElement'))
            .map((row) => {
              row = replaceAll(
                row,
                'function noRefCheck() {}',
                `${titleCase(iconName)}`
              );
              row = replaceAll(
                row,
                'function noRefCheck(){}',
                `${titleCase(iconName)}`
              );
              row = replaceAll(row, `title="${iconName}"`, '');
              row = row.replace(`  `, '');
              return row;
            })
            .filter((row) => !!replaceAll(row, ' ', ''))
            .join('\n');
        }
        return snippet;
      }
    }
  },
  args: {
    source: 'animation-error',
    width: '64px',
    height: '64px',
    loop: true
  },
  argTypes: {
    source: {
      options: Object.keys(data),
      defaultValue: undefined,
      description: 'Animation Data',
      table: {
        type: {
          summary: 'HubAnimationData',
          detail: `import { HubAnimationData } from '@hubert/atoms';`
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'select'
    },
    size: {
      options: size,
      defaultValue: undefined,
      description: 'Style-guide size',
      table: {
        type: {
          summary: 'Size',
          detail: `import { Size } from '@hubert/styles';`
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'inline-radio'
    },
    width: {
      options: size,
      defaultValue: undefined,
      description: 'Set width size',
      table: {
        type: {
          summary: 'String'
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'text'
    },
    height: {
      options: size,
      defaultValue: undefined,
      description: 'Set height size',
      table: {
        type: {
          summary: 'String'
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'text'
    },
    loop: {
      control: 'boolean',
      defaultValue: false,
      description: 'Replay animation on end',
      table: {
        type: {
          summary: 'boolean'
        },
        defaultValue: {
          summary: 'false'
        }
      }
    },
    autoplay: {
      control: 'boolean',
      defaultValue: true,
      description: 'Play animation on init component',
      table: {
        type: {
          summary: 'boolean'
        },
        defaultValue: {
          summary: 'true'
        }
      }
    }
  }
};
