import { HubIcon } from '@hubert/atoms';
import { replaceAll, titleCase } from '@hubert/shared';
import { colorLevels, colorsList, size, spacesList } from '@hubert/styles';
import { Meta } from '@storybook/react';

import allIcons from './icons';

export type HubIconProps = React.ComponentProps<typeof HubIcon>;

export const HubIconStoryConfig: Meta<HubIconProps> = {
  title: 'Design System/Atoms/HubIcon',
  component: HubIcon,
  parameters: {
    docs: {
      description: {
        component: `
    import { HubIcon } from "@hubert/atoms";
    import IconName from '@hubert/styles/lib/icons/<icon-name>.svg';
        `
      },
      transformSource: (snippet, ctx) => {
        if (ctx.name === 'Default') {
          const el = document.getElementById(`story--${ctx.id}`);
          const iconName = el?.children?.[0]?.children?.[1]?.['title'];
          return String(snippet)
            .split('\n')
            .filter((row) => !row.includes('RemoveElement'))
            .map((row) => {
              row = replaceAll(
                row,
                'icon={() => {}}',
                `icon={${titleCase(iconName)}}`
              );
              row = replaceAll(
                row,
                'function noRefCheck() {}',
                `${titleCase(iconName)}`
              );
              row = replaceAll(
                row,
                'function noRefCheck(){}',
                `${titleCase(iconName)}`
              );
              row = replaceAll(row, `title="${iconName}"`, '');
              row = row.replace(`  `, '');
              return row;
            })
            .filter((row) => !!replaceAll(row, ' ', ''))
            .join('\n');
        }
        return snippet;
      }
    }
  },
  args: {
    size: 'lg',
    color: 'primary'
  },
  argTypes: {
    icon: {
      options: Object.keys(allIcons),
      defaultValue: 'icon-outline-add-new',
      description: 'SVG Icon',
      table: {
        type: {
          summary: 'HubIconSvg',
          detail: `import { HubIconSvg } from '@hubert/atoms';`
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'select'
    },
    size: {
      options: size,
      defaultValue: size[1],
      description: 'Style-guide size',
      table: {
        type: {
          summary: 'Size',
          detail: `import { Size } from '@hubert/styles';`
        },
        defaultValue: {
          summary: size[1]
        }
      },
      control: 'inline-radio'
    },
    width: {
      options: size,
      defaultValue: undefined,
      description: 'Set width size',
      table: {
        type: {
          summary: 'String'
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'text'
    },
    height: {
      options: size,
      defaultValue: undefined,
      description: 'Set height size',
      table: {
        type: {
          summary: 'String'
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'text'
    },
    color: {
      options: colorsList,
      defaultValue: undefined,
      description: 'Style-guide color',
      table: {
        type: {
          summary: 'Color',
          detail: `import { Color } from '@hubert/styles';`
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'select'
    },
    colorLevel: {
      options: colorLevels,
      defaultValue: undefined,
      description: 'Style-guide color-level',
      table: {
        type: {
          summary: 'ColorLevel',
          detail: `import { ColorLevel } from '@hubert/styles';`
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'select'
    },
    bgColor: {
      options: colorsList,
      defaultValue: undefined,
      description: 'Style-guide backgroud-color',
      table: {
        type: {
          summary: 'Color',
          detail: `import { Color } from '@hubert/styles';`
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'select'
    },
    bgColorLevel: {
      options: colorLevels,
      defaultValue: undefined,
      description: 'Style-guide backgroud-color-level',
      table: {
        type: {
          summary: 'ColorLevel',
          detail: `import { ColorLevel } from '@hubert/styles';`
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'select'
    },
    pad: {
      options: spacesList,
      defaultValue: undefined,
      description: 'Set padding',
      table: {
        type: {
          summary: 'Space|Space[]',
          detail: `import { Space } from '@hubert/styles';`
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'object'
    }
  }
};
