import { HubGrid, HubSeparator } from '@hubert/atoms';
import React from 'react';

import { HubSeparatorStoryConfig } from './storyConfig';

export const Default = (args) => {
  return (
    <HubGrid row width="600px" height="200px" align="center" justify="center">
      <HubSeparator {...args} />
    </HubGrid>
  );
};

export default HubSeparatorStoryConfig;
