import { HubPagination } from '@hubert/atoms';
import { Meta } from '@storybook/react';

export type HubPaginationProps = React.ComponentProps<typeof HubPagination>;

export const HubPaginationStoryConfig: Meta<HubPaginationProps> = {
  title: 'Design System/Atoms/HubPagination',
  component: HubPagination,
  parameters: {
    docs: {
      description: {
        component: `
    import { HubPagination } from "@hubert/atoms";
        `
      },
      transformSource: (snippet, ctx) => {
        if (ctx.name === 'With Table') {
          return `
        const { data, totalItems, onChange } = usePagination({
  source: hubTableData,
  pageSize: 3
});

<HubCard color="white" pad="3">
  <HubGrid align="end" gap="3">
    <HubTable columns={columnsConfig} data={data} />
    <HubPagination
      showLabel
      showTotalLabel
      pageSize={3}
      totalItems={totalItems}
      onChange={onChange}
    />
  </HubGrid>
</HubCard>
        `;
        }
        if (ctx.name === 'X Accumulate') {
          return `
          const { data, onAccumulate } = usePagination({
  source: hubTableData,
  pageSize: 5
});

return (
  <HubCard color="white">
    <HubTable
      columns={columnsConfig}
      data={data}
      height="120px"
      onLoadMore={onAccumulate}
    />
  </HubCard>
);
        `;
        }
        return snippet;
      }
    }
  },
  args: {
    current: 1,
    totalItems: 50,
    pageSize: 10,
    showLabel: true,
    showSizeChanger: true,
    showTotalLabel: true
  },
  argTypes: {
    current: {
      defaultValue: undefined,
      description: 'Current page selected',
      table: {
        type: {
          summary: 'number'
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'number'
    },
    totalItems: {
      defaultValue: undefined,
      description: 'Total page length',
      table: {
        type: {
          summary: 'number'
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'number'
    },
    disabled: {
      defaultValue: false,
      description: 'Disable buttons',
      table: {
        type: {
          summary: 'boolean'
        },
        defaultValue: {
          summary: 'false'
        }
      },
      control: 'boolean'
    },
    pageSize: {
      defaultValue: undefined,
      description: 'Length of page, items by page',
      table: {
        type: {
          summary: 'number'
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'number'
    },
    pageSizeOptions: {
      defaultValue: undefined,
      description: 'Options in size changer',
      table: {
        type: {
          summary: 'number[]'
        },
        defaultValue: {
          summary: '[5, 10, 15, 25, 50]'
        }
      },
      control: 'object'
    },
    showLabel: {
      defaultValue: false,
      description: 'Show button labels',
      table: {
        type: {
          summary: 'boolean'
        },
        defaultValue: {
          summary: 'false'
        }
      },
      control: 'boolean'
    },
    showSizeChanger: {
      defaultValue: false,
      description: 'Show select page size changer',
      table: {
        type: {
          summary: 'boolean'
        },
        defaultValue: {
          summary: 'false'
        }
      },
      control: 'boolean'
    },
    showTotalLabel: {
      defaultValue: false,
      description: 'Show total results label',
      table: {
        type: {
          summary: 'boolean'
        },
        defaultValue: {
          summary: 'false'
        }
      },
      control: 'boolean'
    },
    noShowIndicators: {
      defaultValue: false,
      description: 'No show number pages',
      table: {
        type: {
          summary: 'boolean'
        },
        defaultValue: {
          summary: 'false'
        }
      },
      control: 'boolean'
    },
    onChange: {
      defaultValue: undefined,
      description: 'Call on page number button click',
      table: {
        type: {
          summary: 'Function',
          detail: '(page?: number) => void'
        },
        defaultValue: {
          summary: 'empty'
        }
      }
    }
  }
};
