import { HubFileUpload } from '@hubert/atoms';
import { colorLevels, colorsList } from '@hubert/styles';
import { Meta } from '@storybook/react';
import React from 'react';

export type HubFileUploadProps = React.ComponentProps<typeof HubFileUpload>;

export const HubFileUploadStoryConfig: Meta<HubFileUploadProps> = {
  title: 'Design System/Atoms/HubFileUpload',
  component: HubFileUpload,
  parameters: {
    docs: {
      description: {
        component: `
    import { HubFileUpload } from "@hubert/atoms";
        `
      },
      transformSource: (snippet, { name }) => {
        return name === 'Complex'
          ? `
const fileList = [
  { name: 'Contrato.pdf', disabled: false, error: '' },
  { name: 'Contrato2.pdf', percent: 75, disabled: false, error: '' },
  {
    name: 'Contrato2.pdf',
    percent: 75,
    disabled: false,
    error: 'Ops! Arquivo no baixado'
  }
];

<HubGrid gap="5">
  {fileList.map((file, i) => (
    <HubCard
      color="neutral"
      colorLevel="200"
      pad="2"
    >
      <HubGrid row justify="space-between" align="center">
        <HubGrid gap="2" width="94%">
          <HubText typo="body1">{file.name}</HubText>
          {file.percent && (
            <HubProgressBar
              color={file.error ? 'error' : 'primary'}
              percent={file.percent}
            />
          )}
          {file.error && (
            <HubText typo="body2" color="error">
              {file.error}
            </HubText>
          )}
        </HubGrid>
        <HubButton
          variant="icon"
          disabled={file.disabled || file.percent === 100}
        >
          <HubIcon icon={file.error ? IconReload : IconClose} />
        </HubButton>
      </HubGrid>
    </HubCard>
  ))}
  <HubFileUpload
    extensions="PDF, DOC e PNG"
    size="5"
  />
</HubGrid>
        `
          : snippet;
      }
    }
  },
  args: {
    extensions: 'PDF, DOC e PNG',
    size: '5M'
  },
  argTypes: {
    title: {
      defaultValue: undefined,
      description: 'Title content',
      table: {
        type: {
          summary: 'string'
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'text'
    },
    extensions: {
      defaultValue: undefined,
      description: 'Extensions content',
      table: {
        type: {
          summary: 'string'
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'text'
    },
    size: {
      defaultValue: undefined,
      description: 'Size content',
      table: {
        type: {
          summary: 'string'
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'text'
    },
    color: {
      options: colorsList,
      defaultValue: undefined,
      description: 'Style-guide color text',
      table: {
        type: {
          summary: 'Color',
          detail: `import { Color } from '@hubert/styles';`
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'select'
    },
    colorLevel: {
      options: colorLevels,
      defaultValue: undefined,
      description: 'Style-guide color-level',
      table: {
        type: {
          summary: 'ColorLevel',
          detail: `import { ColorLevel } from '@hubert/styles';`
        },
        defaultValue: {
          summary: 'empty'
        }
      },
      control: 'select'
    },
    multiple: {
      defaultValue: false,
      description: 'Set multiple file input',
      table: {
        type: {
          summary: 'boolean'
        },
        defaultValue: {
          summary: 'false'
        }
      },
      control: 'boolean'
    },
    onChange: {
      defaultValue: undefined,
      description: 'Call on select/drop file',
      table: {
        type: {
          summary: 'Function',
          detail: '(files: File[]) => void'
        },
        defaultValue: {
          summary: 'empty'
        }
      }
    },
    element: {
      defaultValue: undefined,
      description: 'Replace fileinput style by other element',
      table: {
        type: {
          summary: 'ReactNode'
        },
        defaultValue: {
          summary: 'empty'
        }
      }
    }
  }
};
